.post {
  width: 700px;
  height: 500px;
  background-color: #15202B;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.2rem;
  margin: 30px;
  border-radius: 10px;
}

.post_head {
  padding: 10px;
  box-sizing: border-box;
  display: flex;

}

.post_photo {
  min-width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 10px;
}


.photo {
  width: 100px;
}

.post_title {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
}

.post_title h3,
.post_title span {
  color: grey;
  margin-left: 10px;
}

.post_content {
  width: 80%;
  height: 60%;
  overflow: hidden;
  border-radius: 30px;
  margin-left: 120px;

}

.post_img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}